import React from 'react';
import styles from './Header.module.css';
import { Navbar, Nav, Container } from 'react-bootstrap';

const Header = () => {
  return (
    <Navbar bg="dark" expand="lg" className={styles.navbar}>
      <Container>
        <Navbar.Brand href="/" className={styles.logo}>
          <img src="/logo.png" alt="Logo" className={styles.logoImg} />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ms-auto">
            <Nav.Link href="/dashboard" className={styles.navLink}>Dashboard</Nav.Link>
            <Nav.Link href="/profile" className={styles.navLink}>Profile</Nav.Link>
            <Nav.Link href="/settings" className={styles.navLink}>Settings</Nav.Link>
            <Nav.Link href="/logout" className={styles.logoutBtn}>Logout</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header;
